import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';/* Components importados */
import { CardapioComponent } from './cardapio-component/cardapio.component';
import { CadastroComponent } from './cadastro-component/cadastro.component';
import { HomeComponent } from './home-component/home.component';
import { ItemComponent } from './item-component/item.component';
import { CarrinhoComponent } from './carrinho-component/carrinho.component';
import { componentFactoryName } from '../../node_modules/@angular/compiler';
import { provideForRootGuard } from '../../node_modules/@angular/router/src/router_module';

const routes: Routes = [
    {
        path: '',
        component: HomeComponent
    },
    {
        path: 'cardapio',
        component: CardapioComponent
    },
    {
        path: 'cadastro',
        component: CadastroComponent
    },
    {
        path: 'item',
        component: ItemComponent
    },
    {
        path: 'carrinho',
        component: CarrinhoComponent
    }
];
@NgModule({
    imports: [ RouterModule.forRoot(routes) ],
    exports: [ RouterModule ]
})
export class AppRoutingModule{
    
}