import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.css']
})
export class ItemComponent implements OnInit{
  
  public nome = localStorage.getItem("nome");
  public endereco = localStorage.getItem("endereco");
  public img = localStorage.getItem("img");
  public cardapio1 = localStorage.getItem("cardapio1").split(",");
  public cardapio2 = localStorage.getItem("cardapio2").split(",");
  public cardapio3 = localStorage.getItem("cardapio3").split(",");

  ngOnInit(){
  }

  public c_img: Array<string> = []; 
  public c_nome: Array<string> = []; 
  public c_preco: Array<string> = []; 

  public addCarrinho(nome, img, preco){

    this.c_nome.push(nome);
    console.log(this.c_nome);

    this.c_img.push(img);
    console.log(this.c_img);

    this.c_preco.push(preco);
    console.log(this.c_preco);

    this.carrinhoon = true;
  };

  public carrinhoon: boolean = false;

  public salvar(){
    sessionStorage.setItem("c_nome",this.c_nome.toString());
    sessionStorage.setItem("c_img",this.c_img.toString());
    sessionStorage.setItem("c_preco",this.c_preco.toString());
  }

}